:root {
  /* ðŸŒŒ Background Colors */
  --body-color: hsl(268, 18%, 14%);         /* deep dark blue/black */
 --container-color: linear-gradient(
  135deg,
  rgba(255, 80, 160, 0.42),
  rgba(150, 80, 220, 0.42),
  rgba(60, 140, 255, 0.42)
);





  /* ðŸŽ¨ Neon + Gradient Colors */
   /* neon pink */
  --second-color: #00eaff; /* neon cyan */
  
  /* âš¡ Neon Text Colors */
  
  --title-color: hsl(0, 0%, 100%);   /* light neon bluish white */
  --text-color: #a8b2d1;    /* soft grey-blue */

  /* ðŸŽ‰ Neon Gradient */
  --gradient-primary: linear-gradient(135deg, #ff008c, #ff4d4d, #ffb300);
  --gradient-neon: linear-gradient(135deg, #00eaff, #4d79ff);

  /* ðŸ§ª Typography */
  --body-font: 'Inter';
  --h1-font-size: 2.4rem;
  --h2-font-size: 1.75rem;
  --h3-font-size: 1.3rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.8rem;

  /* ðŸ’ª Font Weights */
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;

  /* ðŸŒ«ï¸ Soft Neon Glow Shadow */
  --shadow: 0 0 25px rgba(255, 0, 140, 0.25);

  /* ðŸŸ£ Card Rounding */
  --border-radius: 22px;
}

@media screen and (max-width: 1024px){
  :root{
    --h1-font-size: 1.75rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size : 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

*{
  margin: 0;
  padding: 0;
  box-sizing:border-box;

}
html{
  scroll-behavior: smooth;
}
body, 
button,
input,
textarea{
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body{
  background-color: var(--body-color);
  line-height: 1.7;
  color: var(--text-color);
}

h1, h2, h3{
  color: var(--title-color);
  font-weight: var(--font-semibold);
  line-height: 1.2;
}

ul{
  list-style: none;
}
a{
  text-decoration: none;

}
button{
  cursor: pointer;
  border: none;
  outline: none;
}

img{
  max-width: 1000%;
  height: auto;
  vertical-align: middle;
}

.container{
  max-width: 1080px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.grid{
  display: grid;
}
.section{
  padding-top: 7rem;
  padding-bottom: 2rem;
}
.section__title{
  font-size: var(--h1-font-size);
  margin-left: 0.875rem;
  font-weight: var(--font-bold);
  position: relative;
  margin-bottom: 3.75rem;
}
.section__title::before{
  content: '';
  background: url(./assets/dots-bg.svg);
  height: 2.25rem;
  width: 2.25rem;
  position: absolute;
  left: -0.875rem;
  top: -0.875rem;

}
.btn{
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 1.875rem;
  box-shadow: 0 0 1px rgb(0 0 0 / 0%);
  border: 1px solid transparent;
  color: #ffff;
  display: inline-block;
  background-color: var(--first-color);
  font-weight: var(--font-bold);

}
@keyframes button-push {
  50%{
    transform: scale(0.8);

  }
  100% {
    transform: scale(1);
  }
}
.btn:hover{
  animation: button-push 0.3s linear 1;
}
