.timeline {
  position: relative;
  display: grid;
  gap: 40px;
  padding-left: 2.5rem; /* was 40px */
}

/* Vertical line behind all items */
.timeline::before {
  content: "";
  position: absolute;
  left: 1rem;
  top: 0;
  width: 3px;
  height: 100%;
  background: white;
  border-radius: 50px;
  opacity: 0.7;
}

/* Each timeline item */
.timeline__item {
  position: relative;
  padding-left: 2.5rem;
  padding-bottom: 0.75rem;
}

.timeline__item:last-child {
  padding-bottom: 0;
}

/* Line inside each item */
.timeline__item::before {
  content: '';
  width: 3px;
  height: 100%;
  background-color: var(--first-color);
  position: absolute;
  left: 1rem;
  top: 0;
}

/* Icon */
.timeline .icon-graduation {
  position: absolute;
  left: -0.1rem;
  top: 0;
  font-size: var(--h2-font-size);
  color: white;
  background-color: var(--container-color);
  padding: 0.3rem;
  border-radius: 50%;
}

/* Text */
.timeline__date {
  color: hsl(245, 20%, 74%);
  font-size: var(--normal-font-size);
}

.timeline__title {
  font-size: var(--h3-font-size);
  margin: 0.5rem 0;
}

.timeline__school {
  font-size: var(--h4-font-size);
}

/* ------------ MOBILE FIXES ------------ */
@media (max-width: 600px) {

  .timeline {
    gap: 25px;
    padding-left: 2rem;
  }

  .timeline::before {
    left: 0.7rem;
    width: 2px;
  }

  .timeline__item {
    padding-left: 2rem;
  }

  .timeline__item::before {
    left: 0.7rem;
    width: 2px;
  }

  .timeline .icon-graduation {
    font-size: 1.1rem;
    left: -0.5rem;
    padding: 0.2rem;
  }
}



